.header {
    background-color: $color-grey-dark-1;

    grid-column: full-start / col-start 6;

    background-image: linear-gradient(
            rgba($color-secondary, 0.93),
            rgba($color-secondary, 0.93)
        ),
        url(../img/hero.jpeg);

    background-size: cover;
    background-position: center;

    padding: 8rem;
    padding-top: 6rem;

    display: grid;

    grid-template-rows: 1fr min-content minmax(min-content, 6rem) 1fr;

    grid-row-gap: 1.5rem;
    justify-content: center;

    @media (max-width: $bp-big) {
        grid-column: 1 /-1;
    }

    &-main {
        font-size: 1.7rem;
    }

    &__logo {
        height: 3rem;
        justify-self: center;
    }

    &__btn {
        align-self: start;
        justify-self: start;
        font-size: 1.5rem;
    }

    &__seenon-text {
        display: grid;
        font-size: 1.6rem;
        color: $color-grey-light-1;

        grid-template-columns: 1fr max-content 1fr;
        align-items: center;
        grid-column-gap: 2rem;
        filter: brightness(50%);

        &::before,
        &::after {
            content: "";
            height: 1px;
            display: block;
            background-color: currentColor;
        }
    }

    &__seenon-logos {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-column-gap: 3rem;
        justify-items: center;
        align-items: center;

        img {
            max-height: 2.5rem;
            max-width: 100%;

            filter: brightness(50%);
        }
    }
}
